import "../styles/private_policy.scss";
import {useHistory} from "react-router-dom";
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
const PrivatePolicy=()=>{
    const history=useHistory();
    return(
        <div className="private_policy">
            <div className="top">
                <button onClick={e=>{
                    history.goBack();
                }}>
                    <ArrowBackIcon />
                </button>
                <h2>Privacy policy</h2>
            </div>
            <div className="content">
            <p><strong>PROSPORT.GURU INC PRIVACY POLICY</strong></p>
<p><strong>Last update: NOVEMBER 15, 2020</strong></p>

<p>In this digital world where personal information can be collected, used and shared with ease, our App ProSport.Guru Inc. (Website) and (App) and the Company that owns and operates the Website and App, (&ldquo;Company&rdquo;) is sensitive to the privacy concerns of our visitors, and we want to let you know that we will handle your personal information with the appropriate level of care. &nbsp;</p>

<p>ProSport.Guru Inc. is an app where people have the opportunity to sell their expertise and share in a revenue model as well as engage in mobile sports games. &nbsp;Accordingly, we have developed a Policy for you to understand how we collect, use, communicate and disclose and make use of personal information. &nbsp;We encourage you to read this Policy and become familiar with it. &nbsp;We may incorporate changes to our Privacy Policy from time to time; therefore, you may wish to check this site periodically to make sure that you have the current version. &nbsp;If you have questions about this Privacy Policy, please contact us.</p>

<p>Our Privacy Policy (the Policy) is an expression of our commitment to adhere not only to the legislative requirements of the Privacy Act concerning the handling of personal information but the spirit of the Act as well. &nbsp;Our objective is to balance the need to collect, retain, use, disclose and dispose of personal information about individuals with the right to privacy of people, and to ensure that all persons are aware of the circumstances under which their personal information will be collected, retained, used, disclosed, and disposed of by the Company. &nbsp;By visiting and/or using the Service on our App, you agree and consent to the collection, use, and transfer of your information as set out in this policy.</p>

<ul>
    <li style={{fontWeight: "bold"}}>
        <p><strong>What Information Does ProSport.Guru Inc. Collect? </strong></p>
    </li>
</ul>

<p>1.1 &nbsp; <strong>Information You Supply</strong></p>
<ul>
    <li>
        <p>Personal Information means any data that may be used to identify a natural person. &nbsp;You may browse our app without revealing any of your Personal Information, however, if you would like to avail yourself to any of our app&apos;s, you will be required to provide a username;</p>
    </li>
    <li>
        <p>When you register to use our Services, we ask for information such as your name, email address;</p>
    </li>
    <li>
        <p>Once you register, you may add information to your profile. &nbsp; &nbsp;</p>
    </li>
    <li>
        <p>If you buy something on or through our Services, we collect information about the transaction. We also acquire such information if you engage in any sports game.</p>
    </li>
</ul>

<p><strong>1.2 Location Information</strong></p>
<ul>
    <li>
        <p>When you communicate with ProSport.Guru Inc you provide us and we collect precise location data, specifically your GPS location.&nbsp;</p>
    </li>
    <li>
        <p>You permit the ProSport.Guru Inc app to access location services through the permission system used by your mobile operating system (&ldquo;platform&rdquo;).</p>
    </li>
    <li>
        <p>We may also collect the precise location of your device when the app is running in the foreground or background.&nbsp;</p>
    </li>
    <li>
        <p>We may also derive your approximate location from your IP address.</p>
    </li>
    <li>
        <p>Your GPS location is a necessary function of this app to be able to provide accurate data as to services offered within the one street away vicinity.</p>
    </li>
</ul>

<p><strong>1.3 Contacts Information</strong></p>
<p>If you permit the ProSport.Guru Inc app to access the address book on your device through the permission system used by your mobile platform, we may access and store names and contact information from your address book to facilitate social interactions through our Services and for other purposes described in this Statement or at the time of consent or collection.</p>

<p><strong>1.4 Transaction Information</strong></p>
<p>We collect transaction details related to your use of our Services, including the type of service requested, date and time the service was provided, amount charged, distance traveled, and other related transaction details. &nbsp;Additionally, if someone uses your promo code, we may associate your name with that person.&nbsp;</p>

<p>ProSport.Guru Inc app will send the customer a message via the personal e-mail address provided for verification once a credit card or PayPal account is linked to the account.&nbsp;</p>

<p><strong>1.5 Usages and Preference Information</strong></p>
<p>We collect information about how you and site visitors interact with our Services, preferences expressed, and settings chosen. &nbsp;In some cases, we do this through the use of cookies, pixel tags, and similar technologies that create and maintain unique identifiers. &nbsp;To learn more about these technologies, please see our Cookie Statement.</p>

<p><strong>1.6 Device Information</strong></p>
<p>We may collect information about your mobile device, including, for example, the hardware model, operating system and version, software and file names and versions, preferred language, unique device identifier, advertising identifiers, serial number device motion information, and mobile network information.</p>
<p>We may, with your permission selection access your Camera to take a photo or allow video messaging onto the ProSport.Guru Inc app.&nbsp;</p>

<p><strong>1.7 Aggregate Information </strong></p>
<ul>
    <li style={{fontWeight: "bold"}}>
        <p><strong>Log Files</strong></p>
    </li>
</ul>
<p>Like many other apps, ProSport.Guru Inc. &nbsp;Makes use of log files. &nbsp;The information inside the log files includes internet protocol (IP) addresses, type of browser, Internet Service Provider (ISP), date/time stamp, referring/exit pages, and number of clicks to analyze trends, administer the site, track user&rsquo;s movement around the site, and gather demographic information. &nbsp;IP addresses, and other such information are not linked to any information that is personally identifiable.&nbsp;</p>
<ul>
    <li style={{fontWeight: "bold"}}>
        <p><strong>Cookies and Web Beacons </strong></p>
    </li>
</ul>
<p>ProSport.Guru Inc does use cookies to store information about visitors preferences, record user-specific information on which pages the user access or visit, customize Web page content based on visitors browser type or other information that the visitor sends via their browser.&nbsp;</p>
<ul>
    <li>
        <p><strong>DoubleClick DART Cooki</strong>e&nbsp;</p>
    </li>
</ul>
<p>Google, as a third party vendor, uses cookies to serve ads on ProSport.Guru Inc. &nbsp;Google&apos;s use of the DART cookie enables it to serve ads to users based on their visit to www.ProSport.guru. &nbsp;And other sites on the Internet. &nbsp;Users may opt out of the use of the DART cookie by visiting the Google ad and content network privacy policy at the following URL -http://www.google.com/privacy_ads.html&nbsp;</p>
<ul>
    <li>
        <p>Some of our advertising partners may use cookies and web beacons on our site. &nbsp;Our advertising partners include Google AdSense. &nbsp;Protocol (&quot;IP&quot;) address, an identifier that is unique to the mobile device on which you install the App as well as particular software and hardware information about your device;</p>
    </li>
    <li>
        <p>The Service pages that you visit and the date and time of your visits Information regarding your access to and use of the App.</p>
    </li>
</ul>

<ul>
    <li>
        <p>How We Use Your Information</p>
    </li>
</ul>
<p>2.1 When we collect personal information from you, such information will only be used for business purposes, such as to personalize the content you view and to better understand how you use the App to improve the services this App provides. &nbsp;</p>
<p>2.2 We use the information to help us provide our Services to you and our partners.&nbsp;</p>
<p>2.3 To provide you with hardware, content, apps, and other services;</p>
<p>2.4 Communicate with you about our Services;</p>
<p>2.5 Provide technical support; and update notifications to our Services; and</p>
<p>2.6 Customize your experiences and improve and develop your experience.&nbsp;</p>
<p>2.7 Improve services offered by others, such as third parties that provide content, apps, and other experiences on our platform.</p>
<p>2.8 We also use this information to measure how users respond to our marketing efforts.</p>

<p><strong>3. Who Can Obtain Information About Me?</strong></p>
<p>3.1 Certain information you post to your profile, such as your ID (also known as your Username), your system-generated user ID, will be public and visible to people in and off our Services. &nbsp;Your ID is a unique identifier you choose to identify yourself on our Services.&nbsp;</p>
<p>3.2 Any other information you choose to share with the third party.</p>
<p>3.3 Sharing Within Related Companies. We may exchange information with local business in your GPS location area. We may also share with companies we partner with such as Pinnacle.com</p>
<p>3.4 We may share with government bodies if required by law to do so.</p>

<p><strong>4. Third Party Advertising and Analytics Services</strong></p>
<p>We may allow others to serve advertisements across the Internet and provide analytics services on our behalf. &nbsp;These entities may use cookies, web beacons and other technologies to collect information about your use of our app and other apps, including your IP address, web browser, pages viewed, time spent on pages, links clicked and conversion information. &nbsp;This information may be used by ProSport.Guru Inc and others to, among other things, analyze and track data, determine the popularity of certain content deliver advertising and content targeted to your interests on our app and other apps and better understand your online activity. &nbsp;If you wish to disable cookies, you may do so through your individual browser options. &nbsp;More detailed information about having your web browsing information used for behavioural advertising purposes, please visit www.aboutads.info/choices and visit www.networkadvertising.org/managing/opt_out.asp. &nbsp;&nbsp;</p>
<p>Our app uses Google Analytics and and/or Mix penal or other similar programs to understand how and from where our site visitors are coming. &nbsp;These third-party ad servers or ad networks use technology in the advertisements and links that appear on ProSport.Guru Inc Other technologies (such as cookies, JavaScript, or Web Beacons) may also be used by the third-party ad networks to measure the effectiveness of their advertisements and / or to personalize the advertising content that you see. &nbsp;The functions and services related to ProSport.Guru Inc are the responsibility of Google. &nbsp;All the cookies, images, videos, statistics, and other components of Google+ are in order of Google&rsquo;s Privacy Statement. &nbsp;ProSport.Guru Inc has no access to or control over these cookies that are used by third-party advertisers. &nbsp;You should consult the respective privacy policies of these third-party ad servers for more detailed information on their practices as well as for instructions about how to opt-out of certain practices. &nbsp;ProSport.Guru Inc&apos;s privacy policy does not apply to, and we cannot control the activities of, such other advertisers or web sites. &nbsp;?</p>

<p><strong>5. THIRD PARTY PLUGINS</strong></p>
<p>Our app uses Social Plugins (&ldquo;Plugins). &nbsp;If you choose to link, create, or log in to your ProSport.Guru Inc account with a payment provider (e.g., PayPal) or Social Media Service (e.g., Facebook), or if you engage with a separate app or app that uses our API (or whose API we use), we may receive information about you or your connections from that site or app.</p>

<p>5.1 Facebook:&nbsp; We use the social network, facebook.com, which is operated by Facebook Inc.; the plugins are identifiable by one of the Facebook logos (white &ldquo;f&rdquo; on a blue background or a &ldquo;thumbs-up&rdquo; sign) or are identified by the phrase &ldquo;Facebook Social Plugin&rdquo;. &nbsp;You may sign in to our site using your Facebook account. &nbsp;http://www.facebook.com/policy.php.&nbsp;</p>
<p>5.2 Twitter Inc.:&nbsp; We use the social network plugins for Twitter Inc., The Twitter plug-in (tweet button), can be recognized by the Twitter logo on our site. &nbsp;When you visit a page of our app, that contains a Twitter button, a direct connection between your browser and the Twitter server is established. &nbsp;Twitter receives the information of your IP address visiting our web page. &nbsp;For more information, refer to the privacy policy of Twitter. &nbsp;If you do not wish that Twitter could assign your visit to our site, please log out from your Twitter account. &nbsp;You may sign in to our site using your Twitter account.</p>
<p>5.3 Google+:&nbsp;Our app uses Google+ Plugins. &nbsp;All the functions and services related to ProSport.Guru Inc app is the responsibility of Google, which is located in 1600 Amphitheatre Parkway, Mountain View, CA 94043, United States. &nbsp;All the cookies, images, videos, statistics, and other components of Google+ are in order of Google&rsquo;s terms. &nbsp;See the Privacy Statement and Privacy Policy of Google+. &nbsp;You may sign in to our site using your Google + account.</p>
<p>5.4 Google analytics is a web analytics tool that helps app owners understand how visitors engage with their apps. &nbsp;Uncivil analytics customers can view a variety of reports about how visitors interact with their apps so they can improve it. &nbsp;Google analytics collects information anonymously. &nbsp;It reports app trends without identifying individual visitors.</p>

<p><strong>6. Important Information About Platform Permissions</strong></p>
<p>Most mobile platforms (iOS, Android, etc.) have defined certain types of device data that apps cannot access without your consent. &nbsp;And these platforms have different permission systems for obtaining your consent. &nbsp;The iOS platform will alert you the first time the ProSport.Guru Inc app wants permission to access certain types of data and will let you consent (or not consent) to that request. &nbsp;Android devices will notify you of the permissions that the ProSport.Guru Inc app seeks before you first use the app, and your use of the app constitutes your consent. &nbsp;Sometimes these permissions require more explanation than the platforms themselves provide, and the permissions we request will change over time, so we&rsquo;ve created these pages to serve as authoritative and up-to-date resources for our users. &nbsp;To learn about the platform-level permissions that the app seeks, please visit our new iOS Permissions page and Android Permissions page.</p>

<p>6.1 Android Devices</p>
<p>Android is a privilege-separated operating system, in which each application runs with a distinct system identity. &nbsp;A central design point of the Android security architecture is that no application, by default, has permission to perform any operations that would adversely impact other applications, the operating system, or the user. &nbsp;This includes reading or writing the user&apos;s private data (such as contacts or emails), reading or writing another application&apos;s files, performing network access, keeping the device awake, and so on.</p>
<p>A basic Android application has no permissions associated with it by default, meaning it cannot do anything that would adversely impact the user experience or any data on the device.&nbsp;</p>

<p>6.2 &nbsp; iPhone</p>
<p>System security is designed so that both software and hardware are secure across all core components of every iOS device. &nbsp;This includes the boot-up process, software updates, and Secure Enclave. &nbsp;This architecture is central to security in iOS, and never gets in the way of device usability. &nbsp;The tight integration of hardware and software on iOS devices ensures that each component of the system is trusted and validates the system as a whole. &nbsp;From initial boot-up to iOS software updates to third-party apps, each step is analyzed and vetted to help ensure that the hardware and software are performing optimally together and using resources properly.&nbsp;</p>
<p>Apple regularly releases software updates to address emerging security concerns and also provide new features; these updates are provided for all supported devices simultaneously. &nbsp;Users receive iOS update notifications on the device and through iTunes, and updates are delivered wirelessly, encouraging rapid adoption of the latest security fixes.&nbsp;</p>
<p>To protect your iPhone&nbsp;against any threats, you need to make sure you&rsquo;re running the latest version of Apple&rsquo;s&nbsp;operating system&nbsp;&ndash; iOS 10.1.1 as of November 1, 2016.</p>

<p><strong>7. International Users</strong></p>
<p>We are registered in Ontario, Canada and, regardless of where you use our Services or otherwise the location in which you provide personal information to us; the data provided may be transmitted to and maintained on servers located in Canada or other countries. &nbsp;By using our Services, you consent to this collection, transfer, storage, and processing of information to and in Canada and other nations.</p>

<p><strong>8. Privacy Protection for Children</strong></p>
<p>We adhere to the Canada Child Protection Laws. &nbsp;We will not market our services or products in any way that exploits children, nor will we collect information from children under 21 without first obtaining the permission of a parent or guardian. &nbsp;</p>
<p>For United States Users to our app, we adhere to the Children&apos;s Online Privacy Protection Act of 1998 (COPPA). &nbsp;This applies to commercial apps that are directed at children under 21 or, even if not so directed, knowingly collect information from children under the age of 21. &nbsp;</p>

<p><strong>9. Push Notifications</strong></p>
<p>We send you push notifications from time-to-time in order to update you about any events or promotions that we may be running. &nbsp;If you no longer wish to receive these types of communications, you may turn them off at the device level. &nbsp;To ensure you receive proper notifications, we will need to collect certain information about your device such as operating system and user identification information.</p>
<p>&nbsp;</p>
<p><strong>10. Cookies Policy</strong></p>
<p>This app uses cookies. &nbsp;A cookie is a small data file that is placed on your hard drive when you visit certain web pages. &nbsp;It is used to identify yourself to an app when you revisit it, to load your preferences and to track the pages you have visited. &nbsp;The only personal information held in such a cookie is information you have provided to us. &nbsp;You have the choice of whether or not to accept cookies by means of your browser settings, although you may not be able to use certain features and functionalities of the ProSport.Guru Inc app if you do disable them.&nbsp;</p>

<p>As you navigate through the app, certain information can be passively collected (that is, gathered without your actively providing the information) using various technologies and means, such as Internet Protocol addresses, cookies, Internet tags, and navigational data collection.??This App may use Internet Protocol (IP) addresses. &nbsp;An IP Address is a number assigned to your computer by your Internet service provider so you can access the Internet and is generally considered to be non-personally identifiable information, because in most cases an IP address is dynamic (changing each time you connect to the Internet), rather than static (unique to a particular user&apos;s computer). &nbsp;We use your IP address to diagnose problems with our server, report aggregate information, determine the fastest route for your computer to use in connecting to our App, and administer and improve services to our consumers.</p>
<p>?A &quot;cookie&quot; is a bit of information that an app sends to your web browser that helps the site remember information about you and your preferences.</p>
<p>?&ldquo;Session cookies&quot; are temporary bits of information that are erased once you exit your web browser window or otherwise turn your computer off. &nbsp;Session cookies are used to improve navigation on apps and to collect aggregate statistical information. &nbsp;This App uses session cookies.</p>
<p>?&ldquo;Persistent cookies&quot; are more permanent bits of information that are placed on the hard drive of your computer and stay there unless you delete the cookie. &nbsp;Persistent cookies store information on your computer for a number of purposes, such as retrieving certain information you have previously provided (e.g., username), helping to determine what areas of the app visitors find most valuable, and customizing the app based on your preferences. &nbsp;This App uses persistent cookies.</p>
<p>?&ldquo;Internet tags&quot; (also known as single-pixel GIFs, clear GIFs, invisible GIFs, and 1-by-1 GIFs) are smaller than cookies and tell the app server information such as the IP address and browser type related to the visitor&apos;s computer. &nbsp;This App uses Internet tags.??&ldquo;Navigational data&quot; (&quot;log files,&quot; &quot;server logs,&quot; and &quot;clickstream&quot; data) and &quot;Internet Tags&quot; are used for system management, to improve the content of the site, market research purposes, and to communicate information to visitors. &nbsp;This App uses navigational data.</p>

<p><strong>11. What Safeguards Are in Place to Protect My Data?</strong></p>
<p>While no app can promise complete security, we have incorporated the latest, technical, and physical security procedures designed to protect your personal data. &nbsp;We secure data of users on our app by using SSL 2048 encryption which is the high-security industry standard. &nbsp;We employ firewalls and intrusion detection systems to help prevent unauthorized persons from gaining access to your information. &nbsp;Our firewalls protect your information from unauthorized access, use, or disclosure.&nbsp;</p>
<p><strong>12. Your Rights</strong></p>
<p>You have the right to access any information we have about you. For any request or question regarding obtaining a copy of personal data we hold relating to you, and/or correction or deletion of your personal data, and/or object to any processing of your personal data, you can send a Subject Access Request to us. We will respond to your request for access and/or correction as soon as possible.</p>
<p>Nevertheless, please note that although your Personal Information may be removed from our databases, ProSport.Guru Inc will retain the anonymous information contained in the data you provided, and such information will continue to be used by us for statistical purpose.</p>
<p>Also, you have the right to:</p>
<p>&bull; &nbsp; &nbsp;Withdraw consent as well as object and restrict further processing of your data in certain circumstances; but this may affect your use of the service</p>
<p>&bull; &nbsp; &nbsp;Request to have your data deleted where there is no good reason for its continued processing and given that there are no legitimate grounds for retaining it</p>
<p>&bull; &nbsp; &nbsp;Request your data to be corrected if it is inaccurate</p>
<p>&bull; &nbsp; &nbsp;Have your data presented in a structured, commonly-used and machine-readable format in certain circumstances.</p>
<p>You have the right to complain to us if you think we have not complied with our obligation for handling your personal information. Any complaint must be made in writing to our Customer Service Support.</p>

<p><strong>13. Contacting Us</strong></p>
<p>We are committed to conducting our business by these principles to ensure that the confidentiality of personal information is protected and maintained. &nbsp;If you have any questions about our privacy practices or this Policy, please contact us at Customer Service Support or email us at: support@prosport.guru</p>

            </div>
        </div>
    )
}

export default PrivatePolicy;